import Vue from 'vue'
import Vuex from 'vuex'
import {
	setLocalStorage,
	getLocalStorage
} from '../utils/index.js'
import Cookies from 'js-cookie'
import {
	getLanguage
} from '@/lang/index'

Vue.use(Vuex);

const store = new Vuex.Store({
	state: {
		/**
		 * 
		 */
		forcedLogin: false,
		hasLogin: false,
		userName: "",
		prePath: '',
		token: getLocalStorage('token'),
		userInfo: {},
		prefix: "http://app.huoju12.com",
		language: getLanguage()
	},
	getters: {
		language: state => state.language
	},
	mutations: {
		LOGIN_OUT(state) {
			state.userName = "";
			state.hasLogin = false;
			state.userInfo = {}
			setLocalStorage('token', '');
		},
		SET_TOKEN(state, token) {
			state.token = token;
			setLocalStorage('token', token);
		},
		LOGIN(state, userInfo) {
			state.userInfo = {
				...state.userInfo,
				...userInfo
			}
			state.hasLogin = userInfo ? true : false
		},
		SET_PRE_PATH(state, path) {
			state.prePath = path
		},
		SET_PREFIX(state, prefix) {
			state.prefix = prefix || state.prefix;
		},
		SET_LANGUAGE(state, language) {
			state.language = language
			// Cookies.set('language', language)
			setLocalStorage('language',language)
		}
	},
	actions: {
		//
		login({
			commit,
			state
		}, params) {
			commit('LOGIN', params)
		},
		//
		setPrePath({
			commit,
			state
		}, params) {
			commit('SET_PRE_PATH', params)
		},
		//ip
		setPrefix({
			commit,
			state
		}, params) {
			commit('SET_PREFIX', params)
		},
		//token
		setToken({
			commit,
			state
		}, params) {
			console.log('setToken------>', params)
			commit('SET_TOKEN', params)
		},
		setLanguage({
			commit
		}, language) {
			commit('SET_LANGUAGE', language)
		}
	}
})

export default store
